import React, { Component } from 'react';
import { coin } from '../../api';
import CoinPresenter from './CoinPresenter';

class Coins extends Component {
    state={
        data:null,
        error:null,
        loading:true,
    }
    async componentDidMount(){
        try{
            const {data} = await coin.Coins()
            this.setState({
                data
            })
        }catch{
            this.setState({
                error:"CANT GET A DATA"
            })
        }finally{
            this.setState({
                loading:false
            })
        }
    }
    render() {
        const {loading,data,error}=this.state;
        return (
            <>
                <CoinPresenter loading={loading} coinData={data} error={error} />
            </>
        );
    }
}

export default Coins;