import React, { Component } from 'react';
import { coin } from '../../api';
import ExchangePresenter from './ExchangePresenter';

class Exchanges extends Component {
    state={
        loading:true,
        data:null,
        error:null,
    }
    async componentDidMount(){
        try{
            const {data} = await coin.exchange();
            this.setState({
                data
            })
        }catch{
            this.setState({
                error:"NO EXCHANGES"
            })
        }finally{
            this.setState({
                loading:false
            })
        }
    }
    render() {
        const {loading,data,error}=this.state
        return (
            <div>
                <ExchangePresenter loading={loading} data={data} error={error} />
            </div>
        );
    }
}

export default Exchanges;
