import { Link, withRouter } from "react-router-dom"
import styled from "styled-components";
const Coder = styled.header`
    display:flex;
`;
const Li = styled.li`
    padding:6px 12px;
    font-size:22px;
    border-radius:20px;
    background-color:${props=>props.current?"orange":"transparent"};
    color:#fff;
`;

const Header = ({location})=>{
    const {pathname}=location;
    return (
        <Coder>
            <Li current={pathname === "/homepage"}>
                <Link to="/homepage">Prices</Link>
            </Li>
            <Li current={pathname === "/exchange"}>
                <Link to="/exchange">Exchanges</Link>
            </Li>
            <Li current={pathname === "/coins"}>
                <Link to="/coins">Coins</Link>
            </Li>
        </Coder>
    )
}

export default withRouter(Header);