import Loader from "../../components/Loader";

const CoinPresenter = (props)=>{
    const {loading,coinData,error}=props
    return(
        <>
            <h1>Coins</h1>
            {loading?<Loader/> : coinData.map(coin=><li> #{coin.rank} {coin.name} / {coin.symbol}</li>) }
        </>
    )
}

export default CoinPresenter;