import React from "react";
import { HashRouter, Route, Switch } from "react-router-dom";
import Coins from "../screens/Coins/Coins";
import Exchanges from "../screens/Exchange/Exchanges";
import Prices from "../screens/Prices/Prices";
import Header from "./Header";

const Router = ()=>{
    return(
        <HashRouter>
            <Header />
            <Switch>
                <Route exact path="/exchange" component={Exchanges} />
                <Route exact path="/coins" component={Coins} />
                <Route exact path="/homepage" component={Prices} />
            </Switch>
        </HashRouter>
    )
}

export default Router;