import axios from "axios";


const getApi = axios.create({
    baseURL: 'https://api.coinpaprika.com/v1/',
});

export const coin = {
    home:()=> getApi.get("tickers"),
    Coins:()=> getApi.get("coins"),
    exchange:()=>getApi.get("exchanges")
}

export default getApi;


